<template>
  <div>
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/soc-attack-event' }">运营中心</el-breadcrumb-item>
        <el-breadcrumb-item>攻击事件</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <div class="query-time-container">
          <el-select v-model="valueTime" placeholder="Select" v-show="isShowSelectTime" @change="onChangeSelectTime">
            <el-option v-for="item in optionTime" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
          <div v-show="!isShowSelectTime">
            <el-date-picker v-model="pickerTime" type="datetimerange" range-separator="-" start-placeholder="开始时间"
              end-placeholder="结束时间" @change="changeTimeline" />
          </div>
          <el-button icon="el-icon-search" @click="onChangeSearch" class="search-icon-btn" />
        </div>
        <div class="demo-block">
          <el-table :data="tableData" style="width: 100%">
            <el-table-column prop="AttackIP" label="攻击IP"></el-table-column>
            <el-table-column label="攻击详情">
              <template #default="scope">
                <p v-if="logConfig == 'true'">
                  <span class="col-item-protection-title">请求总数：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.AttackCount }}</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">攻击次数：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.AttackCount }}</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">拦截次数：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.BlockCount }}</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">攻击接口数量：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.UniqueAttackInterfaces }}</el-tag>
                  </span>
                </p>
                <p>
                  <span class="col-item-protection-title">拦截接口数量：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.UniqueBlockedInterfaces }}</el-tag>
                  </span>
                </p>
              </template>
            </el-table-column>
            <el-table-column label="防护策略">
              <template #default="scope">
                <p v-for="(item, key) in scope.row.AttackTypes" :key="key">{{ item }}</p>
              </template>
            </el-table-column>
            <el-table-column label="时间" width="250">
              <template #default="scope">
                <p v-if="logConfig == 'false'">
                  <span class="col-item-protection-title">开始攻击时间：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.StartTime }}</el-tag>
                  </span>
                </p>
                <p v-if="logConfig == 'false'">
                  <span class="col-item-protection-title">最新攻击时间：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.LatestTime }}</el-tag>
                  </span>
                </p>
                <p v-if="logConfig == 'true'">
                  <span class="col-item-protection-title">开始请求时间：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.FirstRequestTime }}</el-tag>
                  </span>
                </p>
                <p v-if="logConfig == 'true'">
                  <span class="col-item-protection-title">开始攻击时间：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.FirstAttackTime }}</el-tag>
                  </span>
                </p>
                <p v-if="logConfig == 'true'">
                  <span class="col-item-protection-title">最新攻击时间：</span>
                  <span>
                    <el-tag size="small">{{ scope.row.LatestAttackTime }}</el-tag>
                  </span>
                </p>
              </template>
            </el-table-column>
            <el-table-column label="操作" align="right">
              <template #default="scope">
                <el-button size="mini" @click="handleLookBehave(scope.row)" class="button-block" type="text">查看行为轨迹
                </el-button>
                <el-button size="mini" @click="handleLookAttack(scope.row)" class="button-block" type="text">查看攻击详情
                </el-button>
                <el-button size="mini" @click="handleJoin(scope.row)" class="button-block" type="text"
                  :loading="loading">加入名单防护
                </el-button>
              </template>
            </el-table-column>
          </el-table>

          <el-pagination background layout="prev, pager, next, sizes" :total="tableTotal"
            :page-sizes="[10, 20, 50, 100]" v-model:current-page="currentPage" v-model:page-size="pageSize"
            @current-change="onCurrentChange" @size-change="handleSizeChange" />
        </div>
      </el-col>
    </el-row>

    <el-dialog v-model="dialogJoinFormVisible" title="加入名单防护" :close-on-click-modal="false" width="520px"
      @closed="dialogCloseJoin">
      <el-form class="form-download-rule-dialog" :model="joinForm" label-position="right" label-width="130px"
        :rules="rules" ref="joinForm">
        <el-form-item label="名单" key="1" prop="name_list_name">
          <el-select v-model="joinForm.name_list_name" placeholder="请选择">
            <el-option v-for="item in nameList" :key="item.name_list_name" :label="item.name_list_name"
              :value="item.name_list_name">
            </el-option>
          </el-select>
        </el-form-item>

      </el-form>

      <template #footer class="dialog-footer">
        <el-button @click="dialogJoinFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onClickJoinSubmit('joinForm')" :loading="loading">确定
        </el-button>
      </template>
    </el-dialog>
  </div>

</template>
<script>
import { mixin, JXAjax, formatterDateTime } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      tableData: [],
      currentPage: 1,
      tableTotal: 0,
      pageSize: 20,
      isShowSelectTime: true,
      valueTime: "1w",
      pickerTime: [],
      optionTime: [
        { value: '1h', label: '1小时' },
        { value: '1d', label: '24小时' },
        { value: '1w', label: '7天' },
        { value: '1m', label: '30天' },
        { value: 'default', label: '自定义' }
      ],
      logConfig: "false",
      joinForm: {},
      dialogJoinFormVisible: false,
      nameList:[],
    };
  },
  computed: {
    rules() {
      return {
        name_list_name: [
          {
            required: true,
            message: '请选择名单',
            trigger: ["blur", "change"],
          },
        ],
      }
    }
  },

  mounted() {
    this.getLogConf();
  },
  methods: { 
    getData(page) {
      var t = this;
      var url = "/soc/soc_attack_event_get_list";
      if (t.logConfig == 'true') { 
        url = "/soc/soc_attack_event_get_all_log_list";
      }
      JXAjax(
        "post",
        url,
        {
          from_time: formatterDateTime(t.pickerTime[0]),
          to_time: formatterDateTime(t.pickerTime[1]),
          page_size: t.pageSize,
          page_number: page
        },
        function (response) {
          t.tableData = response.data.data;
          t.tableData.forEach(item => {
            item.isVisiblePopover = false;
          })
          t.tableTotal = response.data.total_count;
          t.currentPage = response.data.now_page;
        },
        function () {

        },
        "no-message"
      );
    },
    getLogConf() { 
      var t = this;
      var getUrl = "/waf/waf_get_sys_log_conf";
      JXAjax(
        "post",
        getUrl,
        {},
        function (response) {
          t.loadingPage = false;
          t.logConfig = response.data.message.log_all;
          t.onChangeSearch();
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    handleLookBehave(data) {
      window.location.href = "/#/soc-attack-event-behave/" + data.AttackIP;
    },
    handleLookAttack(data) { 
      var t = this;
      var time = {};
      time.type = t.valueTime;
      time.from_time = formatterDateTime(t.pickerTime[0]);
      time.to_time = formatterDateTime(t.pickerTime[1]);
      window.open("/#/soc-query-log/" + data.AttackIP + '/' + JSON.stringify(time));
    },
    handleJoin(data) { 
      var t = this;
      var getUrl = "/waf/waf_get_name_list_list";
      t.joinForm.name_list_item = data.AttackIP;
      t.loading = true;
      JXAjax(
        "post",
        getUrl,
        {},
        function (response) {
          t.loading = false;
          t.nameList = response.data.message;
          t.dialogJoinFormVisible = true;
        },
        function () {
          t.loading = false;
        },
        "no-message"
      );
    },
    dialogCloseJoin() {
      this.joinForm = {};
      this.$refs["joinForm"].resetFields();
    },
    onClickJoinSubmit(joinForm) {
      var t = this;
      var postUrl = "/waf/waf_create_name_list_item";
      this.$refs[joinForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.joinForm,
            function (response) {
              t.loading = false;
              t.dialogJoinFormVisible = false;
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    onCurrentChange() {
      this.onChangeSelectTime()
      this.getData(this.currentPage)
    },
    onChangeSearch() {
      this.onChangeSelectTime()
      this.getData(1)
    },
    handleSizeChange() { 
      
    },
    onChangeSelectTime() {
      var t = this;
      if (t.valueTime == 'default') {
        t.isShowSelectTime = false;
        t.pickerTime = [
          new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
          new Date()
        ];
      } else {
        t.isShowSelectTime = true;
        if (t.valueTime == '1h') {
          t.pickerTime = [
            new Date(new Date().getTime() - 1 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1d') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000),
            new Date()
          ];
        }
        if (t.valueTime == '1w') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 7),
            new Date()
          ];
        }
        if (t.valueTime == '1m') {
          t.pickerTime = [
            new Date(new Date().getTime() - 24 * 60 * 60 * 1000 * 30),
            new Date()
          ];
        }
      }
    },
    changeTimeline(event) {
      var t = this;
      if (event == null) {
        t.isShowSelectTime = true;
        t.valueTime = '1w'
      } else {
        t.isShowSelectTime = false;
      }
    },
  },
};
</script>
<style>
.query-time-container{
  display: flex;
}
.query-time-container .el-button {
  margin-left: 10px;
}
.col-item-protection-title {
  display: inline-block;
  width: 100px;
}
.el-table__body p {
  font-size: 14px;
  line-height: 30px;
}
</style>
